export function getPixelRatio() {
  return typeof window === 'object' && window.devicePixelRatio ? window.devicePixelRatio : 1;
}

/**
 * 
 * @param {number} x1  x
 * @param {number} y1  y
 * @param {number} x2  x
 * @param {number} y2  y
 */
export function distance(x1: number, y1: number, x2: number, y2: number) {
  const dx = x1 - x2;
  const dy = y1 - y2;
  return Math.sqrt(dx * dx + dy * dy);
}

/**
 * 
 * @param {number} minX    x
 * @param {number} minY    y
 * @param {number} width  
 * @param {number} height 
 * @param {[type]} x       x
 * @param {[type]} y       y
 */
export function inBox(minX: number, minY: number, width: number, height: number, x, y) {
  return x >= minX && x <= minX + width && y >= minY && y <= minY + height;
}

export function intersectRect(box1, box2) {
  return !(box2.minX > box1.maxX || box2.maxX < box1.minX || box2.minY > box1.maxY || box2.maxY < box1.minY);
}

// 
export function mergeRegion(region1, region2) {
  if (!region1 || !region2) {
    return region1 || region2;
  }
  return {
    minX: Math.min(region1.minX, region2.minX),
    minY: Math.min(region1.minY, region2.minY),
    maxX: Math.max(region1.maxX, region2.maxX),
    maxY: Math.max(region1.maxY, region2.maxY),
  };
}

/**
 *  [x, y]
 * @param {Array} point1 
 * @param {Array} point2 
 */
export function isSamePoint(point1, point2) {
  return point1[0] === point2[0] && point1[1] === point2[1];
}

export { isNil, isString, isFunction, isArray, each, toRadian, mod, isNumberEqual } from '@antv/util';

export { requestAnimationFrame, clearAnimationFrame } from './time';

export function tick(fn: FrameRequestCallback) {
  if (window) {
    requestAnimationFrame(fn);
  } else {
    // TODO need a timeline to control time
    setTimeout(fn, 16);
  }
}

// 
export function isParent(container, shape) {
  //  shape  canvas 
  if (container.isCanvas()) {
    return true;
  }
  let parent = shape.getParent();
  let isParent = false;
  while (parent) {
    if (parent === container) {
      isParent = true;
      break;
    }
    parent = parent.getParent();
  }
  return isParent;
}
